___CSS_LOADER_EXPORT___.push([module.id, `.game-view {
  display: flex;
  gap: 2em;
  flex-direction: column;
  margin: 0 auto;
  width: 100vw;
  min-height: 100vh;
  margin-top: 5em;
  background-color: black;
  margin-top: 0;
  padding-top: 2em;

  justify-content: center;
}

.game-view-cell {
  width: calc(min(85vh, 45vw) / 10);
  height: calc(min(85vh, 45vw) / 10);
  background: var(--white);
}

.game-view-board {
  width: max-content;
  margin: 0 auto;
  height: max-content;
}

@media screen and (min-width: 768px) {
  .game-view-cell {
    width: calc(min(45vh, 45vw) / 10);
    height: calc(min(45vh, 45vw) / 10);
    background: var(--white);
  }

  .game-view {
    flex-direction: row;
    align-items: center;
  }
}
