___CSS_LOADER_EXPORT___.push([module.id, `.start-view {
  background: var(--black);
  width: 100vw;
  height: 100vh;
  color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.sub-heading {
  margin-top: 12em;
  color: var(--yellow);
  font-size: 1.07rem;
  text-align: center;
  text-transform: uppercase;
}

.heading {
  margin-top: 0.2em;
  display: block;
  text-align: center;
  color: var(--white);
  font-family: "Rubik Mono One", sans-serif;
  font-size: 2rem;
  margin-bottom: 0.8em;
}

.start-btn {
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--blue);
  padding: 0.3em 0.5em;
  transition:
    backround-color 200ms ease-in,
    color 200ms ease-in;
  border-radius: 5px;
}

.start-btn:hover {
  background: var(--blue);
  color: var(--white);
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .sub-heading {
    font-size: 2rem;
  }

  .heading {
    font-size: 4rem;
  }

  .start-btn {
    font-size: 2rem;
  }
}
